.navbarScroll.navbarDark {
    background-color: black;
}

nav {
    background-color: black;
}

.bgimage {
    height: 100vh;
    background: url(images/programimage.jpeg);
    background-size: cover;
    position: relative;
}

.hero_title {
    font-size: 4.5rem;
}

.hero_desc {
    font-size: 2rem;
}

.hero-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}

#contact {
    padding-bottom: 4rem;
}

.imageAboutPage {
    width: 85%;
}

.servicesText.card {
    height: 280px;
    cursor: pointer;
}

.servicesIcon {
    font-size: 36px;
    text-align: center;
    width: 100%;
}

.card-title {
    text-align: center;
}

.card:hover .servicesIcon {
    color: #008000;
}

.servicesText:hover {
    border: 1px solid #008000;
}

.social-icons {
    font-size: 36px;
    cursor: pointer;
}

.fa-facebook:hover, .fa-instagram:hover, .fa-linkedin:hover, .fa-twitch:hover {
    color: #008000;
}

.fab {
    color: black;
}

#footer {
    background-color: #808080;
    text-align: center;
}